.stars {
  display: flex;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url("../images/star.svg");
    background-repeat: no-repeat;
    background-position: 50%;

    &:not(:last-child) {
      margin-right: 4px;
    }
  }
  
  & :nth-child(-n + 4) {
    background-image: url("../images/star-active.svg");
  }
}
